@font-face {
  font-family: "Roboto-Bold";
  src: url("../fonts/Roboto-Bold.woff2") format("woff2"), url("../Roboto-Bold.woff") format("woff");
  font-weight: weight;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Roboto-Light";
  src: url("../fonts/Roboto-Light.woff2") format("woff2"), url("../Roboto-Light.woff") format("woff");
  font-weight: weight;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Roboto-Regular";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../Roboto-Regular.woff") format("woff");
  font-weight: weight;
  font-display: swap;
  font-style: normal;
}

:root {
  --ease: cubic-bezier(0.42, 0, 0.58, 1);
  --ease-in: cubic-bezier(0.46, 0.04, 0.66, 0.32);
  --ease-out: cubic-bezier(0.25, 0.46, 0.45, 0.94);
  --ease-out-strong: cubic-bezier(0.23, 1, 0.32, 1);
  --bounce: cubic-bezier(0.655, 0.66, 0.185, 1.265);
}

body {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 16px;
  color: #373737;
  font-family: "Roboto", "Arial", sans-serif;
  background-color: #f7f9f8;
}

body.open-modal {
  overflow-y: hidden;
}

.full {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  min-height: 100vh;
}

header {
  position: relative;
  z-index: 5000;
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: auto;
}

main {
  flex-grow: 1;
  flex-shrink: 0;
  flex-basis: auto;
}

.wrapper {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 20px;
}

button:focus {
  outline: none;
}

.white-container {
  background-color: #ffffff;
}

.white-container--bg {
  background-image: url("../../img/grid.png");
  background-repeat: no-repeat;
  background-size: auto;
  background-position: right;
}

@media (max-width: 1200px) {
  .white-container--bg {
    background-image: none;
  }
}

.main-title,
h1 {
  position: relative;
  display: inline-block;
  margin-bottom: 60px;
  font-size: 26px;
  font-weight: 700;
  line-height: 30px;
  z-index: 2;
}

.main-title--light::before,
h1--light::before {
  content: "";
  position: absolute;
  top: 0;
  right: 20%;
  bottom: 0;
  width: 256%;
  height: 80px;
  margin: auto;
  background-color: #ffffff;
  z-index: -1;
}

.main-title--grey::before,
h1--grey::before {
  content: "";
  position: absolute;
  top: 0;
  right: 20%;
  bottom: 0;
  width: 256%;
  height: 80px;
  margin: auto;
  background-color: #f7f9f8;
  z-index: -1;
}

.main-title--center {
  display: block;
  margin-bottom: 30px;
  text-align: center;
}

.main-title--center::before {
  content: none;
}

h2,
.title-h2 {
  font-size: 48px;
  line-height: 30px;
}

h3,
.title-h3 {
  font-size: 48px;
  line-height: 30px;
}

h4,
.title-h4 {
  font-size: 48px;
  line-height: 30px;
}

h5,
.title-h5 {
  font-size: 48px;
  line-height: 30px;
}

h6,
.title-h6 {
  font-size: 48px;
  line-height: 30px;
}

p {
  margin: 20px 0;
  margin-top: 0;
  font-size: 16px;
  font-weight: 300;
  line-height: 26px;
  color: #373737;
}

a {
  position: relative;
  color: #373737;
  text-decoration: none;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  cursor: pointer;
}

a:focus,
a:hover {
  text-decoration: none;
}

ul li {
  list-style-type: none;
}

img {
  max-width: 100%;
}

.text {
  font-size: 16px;
  font-weight: 300;
  line-height: 26px;
  color: #373737;
}

.text--center {
  text-align: center;
}

.nice-select {
  display: flex;
  align-items: center;
  height: 56px;
  line-height: 1.5;
  white-space: normal;
  border-color: transparent;
}

.nice-select:after {
  border-bottom: 2px solid #08CC99;
  border-right: 2px solid #08CC99;
}

progress {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 3;
  appearance: none;
  -webkit-appearance: none;
  width: 100%;
  height: 5px;
  border: none;
  background-color: transparent;
}

progress::-webkit-progress-bar {
  background-color: transparent;
}

progress::-webkit-progress-value {
  background-color: #08CC99;
  background-attachment: fixed;
}

progress::-moz-progress-bar {
  background-color: #08CC99;
  background-attachment: fixed;
}

.gs_reveal {
  opacity: 0;
  visibility: hidden;
  transition: transform 0s;
}

.arrow-slider {
  display: flex;
  justify-content: space-between;
  align-content: center;
  width: 200px;
  height: 35px;
}

.swiper-button-prev,
.swiper-button-next {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
  margin-left: 20px;
  background-image: none;
  background-color: transparent;
  border: 1px solid #08CC99;
  border-radius: 100%;
  transition: border 0.3s linear;
}

.swiper-button-prev svg,
.swiper-button-next svg {
  width: 20px;
  height: 10px;
  stroke: #286E56;
}

.page-header {
  position: relative;
}

.page-header::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  z-index: 2;
  width: 100%;
  height: 40px;
  background-image: url("../../img/overlay.png");
  background-size: 100%;
  background-position: center;
  background-repeat: no-repeat;
}

.page-header__item:nth-child(1) {
  grid-column: span 3;
}

.page-header__item:nth-child(2) {
  grid-column: span 6;
}

.page-header__item:nth-child(3) {
  grid-column: span 3;
}

.page-header__item:nth-child(3) .phone {
  display: none;
}

.page-header--inner {
  margin-bottom: 30px;
}

.page-header--inner .page-header__item:nth-child(3) .phone {
  display: block;
  text-align: right;
}

.page-header__wrapper {
  position: relative;
  z-index: 2;
  max-width: 99%;
  margin: 0 auto;
  margin-top: 3px;
  background-color: #ffffff;
}

.page-header__wrapper > .phone {
  display: none;
}

.page-header__inner {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  align-items: center;
}

.page-header__submenu {
  background-color: #EBEBEB;
}

.page-header__submenu .wrapper {
  padding: 0 60px;
}

@media (max-width: 1000px) {
  .page-header__wrapper {
    padding: 10px;
  }
  .page-header__item:nth-child(1) {
    grid-column: span 12;
    text-align: center;
  }
  .page-header__item:nth-child(2) {
    display: none;
  }
  .page-header__submenu {
    display: none;
  }
  .page-header--inner .page-header__wrapper > .phone,
  .page-header__wrapper > .phone {
    position: absolute;
    top: 7px;
    right: 10px;
    display: block;
    width: 40px;
    height: 40px;
    padding: 10px;
    background: transparent;
    border: 1px solid #08CC99;
    border-radius: 100%;
  }
  .page-header__wrapper > .phone span {
    display: none;
  }
  .page-header--inner .page-header__item:nth-child(3) .phone {
    display: none;
  }
}

.submenu {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: space-between;
}

.submenu li a {
  position: relative;
  display: block;
  padding: 8px 40px;
  color: #373737;
  font-size: 18px;
  font-weight: 300;
  line-height: 26px;
}

.submenu li a::before {
  content: "";
  position: absolute;
  bottom: 0;
  height: 2px;
  background-color: #ffffff;
  left: 0;
  width: 100%;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  -webkit-transform-origin: right;
  transform-origin: right;
  -webkit-transition-property: color, background-color, -webkit-transform;
  transition-property: color, background-color, -webkit-transform;
  transition-property: color, transform, background-color;
  transition-property: color, transform, background-color, -webkit-transform;
  -webkit-transition-duration: 0.7s;
  transition-duration: 0.7s;
  -webkit-transition-timing-function: ease;
  transition-timing-function: ease;
}

.submenu li a.active::before, .submenu li a:hover::before {
  opacity: 1;
  background-color: #08CC99;
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
  -webkit-transform-origin: left;
  transform-origin: left;
}

.main-menu {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.main-menu li {
  border-left: 1px solid #EBEBEB;
}

.main-menu li:last-child {
  border-right: 1px solid #EBEBEB;
}

.main-menu li a {
  position: relative;
  display: block;
  padding: 13px 40px;
  color: #373737;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 300;
  line-height: 26px;
}

.main-menu li a::before {
  content: "";
  position: absolute;
  bottom: 0;
  height: 2px;
  background-color: #ffffff;
  left: 0;
  width: 100%;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  -webkit-transform-origin: right;
  transform-origin: right;
  -webkit-transition-property: color, background-color, -webkit-transform;
  transition-property: color, background-color, -webkit-transform;
  transition-property: color, transform, background-color;
  transition-property: color, transform, background-color, -webkit-transform;
  -webkit-transition-duration: 0.7s;
  transition-duration: 0.7s;
  -webkit-transition-timing-function: ease;
  transition-timing-function: ease;
}

.main-menu li a.active::before, .main-menu li a:hover::before {
  opacity: 1;
  background-color: #08CC99;
  -webkit-transform: scaleX(1);
  transform: scaleX(1);
  -webkit-transform-origin: left;
  transform-origin: left;
}

@media (max-width: 1450px) {
  .main-menu li a {
    padding: 13px 15px;
    font-size: 16px;
  }
}

.magic-link {
  position: relative;
  display: inline-block;
  padding-bottom: 3px;
  text-transform: uppercase;
  cursor: pointer;
}

.magic-link::before {
  content: "";
  position: absolute;
  bottom: 0px;
  height: 2px;
  background-color: #08CC99;
  left: 0;
  width: 100%;
  opacity: 1;
  transform: scaleX(1);
  transform-origin: right;
  transition-property: color, transform, background-color;
  transition-duration: 0.7s;
  transition-timing-function: ease;
}

.magic-link:hover {
  color: #08CC99;
}

.magic-link:hover::before {
  background-color: transparent;
  transform: scaleX(0);
  transform-origin: left;
}

span.magic-link {
  position: relative;
  padding-bottom: 3px;
  text-transform: uppercase;
}

span.magic-link::before {
  content: "";
  position: absolute;
  bottom: 0px;
  height: 2px;
  background-color: #08CC99;
  left: 0;
  width: 100%;
  opacity: 1;
  transform: scaleX(1);
  transform-origin: right;
  transition-property: color, transform, background-color;
  transition-duration: 0.7s;
  transition-timing-function: ease;
}

span.magic-link:hover {
  color: #373737;
}

span.magic-link:hover::before {
  background-color: #08CC99;
  transform: scaleX(1);
}

.magic-box svg {
  position: absolute;
  top: 0;
  left: 0;
}

.magic-box svg line {
  stroke-width: 1;
  stroke: transparent;
  fill: none;
  -webkit-transition: all 0.7s ease-in-out;
  transition: all 0.7s ease-in-out;
}

.magic-box:hover svg line {
  -webkit-transition-delay: 0.1s;
  transition-delay: 0.1s;
}

.magic-box svg line.top,
.magic-box svg line.bottom {
  stroke-dasharray: 330 240;
}

.magic-box svg line.left,
.magic-box svg line.right {
  stroke-dasharray: 525 329;
}

.magic-box:hover svg line.top {
  -webkit-transform: translateX(-600px);
  transform: translateX(-600px);
}

.magic-box:hover svg line.bottom {
  -webkit-transform: translateX(0);
  transform: translateX(0);
}

.magic-box:hover svg line.left {
  -webkit-transform: translateY(920px);
  transform: translateY(920px);
}

.magic-box:hover svg line.right {
  -webkit-transform: translateY(-861px);
  transform: translateY(-861px);
}

.magic-box:hover svg line {
  stroke: #41ad62;
}

.phone {
  color: #373737;
  font-size: 18px;
  font-weight: 700;
  line-height: 21px;
  text-align: center;
}

.phone svg {
  width: 15px;
  height: 15px;
  fill: #08CC99;
}

.title-block {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 50px;
}

@media (max-width: 600px) {
  .title-block {
    justify-content: center;
    margin-bottom: 20px;
  }
}

.title-block__item {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 40px;
}

.title-block__btn {
  margin-left: 50px;
}

.title-block .main-title,
.title-block .inner-title {
  margin-bottom: 0;
}

@media (max-width: 1000px) {
  .title-block__btn {
    margin-left: 0;
  }
}

.main-slider {
  height: calc(100vh - 120px);
}

.main-slider__inner {
  position: relative;
}

.main-slider__inner img {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: 100%;
  height: auto;
  object-fit: cover;
}

.main-slider__info {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: flex-start;
  justify-content: center;
  height: 100%;
}

.main-slider__info div {
  width: 50%;
}

.main-slider__title {
  position: relative;
  z-index: 1;
  margin-bottom: 40px;
  font-size: 4rem;
  font-weight: 700;
  line-height: 1.2;
}

.main-slider__tagline {
  position: relative;
  z-index: 1;
  color: #373737;
  font-size: 1.6rem;
  font-weight: 700;
  line-height: 1.5;
}

.main-slider .swiper-slide {
  overflow: hidden;
}

.main-slider .slide-inner {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-size: cover;
  background-position: center;
}

.swiper-container-horizontal > .swiper-pagination-bullets {
  bottom: 20%;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  max-width: 1440px;
  margin: auto;
  padding: 0 20px;
}

.swiper-pagination-bullet {
  position: relative;
  display: inline-block;
  width: 15px;
  height: 15px;
  background-color: #ffffff;
  border-radius: 100%;
  opacity: 1;
}

.swiper-pagination-bullet-active {
  width: 18px;
  height: 18px;
  background: transparent;
  border: 1px solid #08CC99;
  transition: background 0.3s linear;
}

.swiper-pagination-bullet-active::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 10px;
  height: 10px;
  background: #08CC99;
  border-radius: 100%;
}

@media (max-width: 1000px) {
  .main-slider {
    height: 300px;
  }
  .main-slider__title {
    font-size: 2rem;
  }
}

@media (max-width: 600px) {
  .main-slider__info div {
    width: 100%;
  }
  .main-slider__tagline {
    font-size: 0.8rem;
  }
  .swiper-container-horizontal > .swiper-pagination-bullets {
    bottom: 10px;
  }
}

.section__inner {
  padding: 80px 0;
  overflow: hidden;
}

@media (max-width: 600px) {
  .section__inner {
    padding: 30px 0;
  }
}

.feauters {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: center;
}

.feauters__item {
  flex-basis: 25%;
  border-left: 1px solid #EBEBEB;
}

.feauters__item:last-child {
  border-right: 1px solid #EBEBEB;
}

.feauters__card {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  padding: 50px 0;
}

.feauters__card svg {
  width: 53px;
  height: 53px;
  margin-right: 15px;
  fill: #939393;
}

.feauters__card span {
  width: 45%;
  margin-left: 15px;
  font-size: 16px;
  font-weight: 300;
  line-height: 1.5;
}

@media (max-width: 1200px) {
  .feauters__item {
    flex-basis: 50%;
    border-left: none;
  }
  .feauters__item:last-child {
    border-right: none;
  }
  .feauters__card {
    padding: 20px 0;
  }
}

@media (max-width: 600px) {
  .feauters__card span {
    font-size: 14px;
  }
  .feauters__card svg {
    width: 30px;
    height: 30px;
    margin-right: 0;
    margin-bottom: 10px;
  }
  .feauters__card span {
    width: 100%;
    margin-left: 0;
    text-align: center;
  }
}

.services-list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  grid-gap: 30px;
}

@media (max-width: 600px) {
  .services-list {
    grid-template-columns: repeat(1, 1fr);
  }
}

.services-card {
  padding: 12px;
  background-color: #ffffff;
  filter: drop-shadow(0px 4px 66px rgba(0, 0, 0, 0.03));
}

.services-card__info {
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: #ffffff;
}

.services-card__info-inner {
  position: relative;
  display: grid;
  grid-template-columns: repeat(2, auto);
  align-items: center;
  padding: 30px 50px;
  padding-right: 30px;
}

.services-card__info-inner a {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  width: 100%;
  height: 100%;
}

.services-card__icon {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
  width: 60px;
  height: 60px;
  margin-left: 20px;
  border: 1px solid #08CC99;
  border-radius: 100%;
  transition: border 0.3s linear;
}

.services-card__info:hover .services-card__icon {
  border: 3px solid #08CC99;
}

.services-card__icon svg {
  width: 20px;
  height: 10px;
  stroke: #286E56;
}

.services-card__title {
  font-size: 26px;
  font-weight: 700;
  line-height: 30px;
}

.services-card__tagline {
  font-size: 16px;
  line-height: 26px;
}

@media (max-width: 700px) {
  .services-card img {
    display: none;
  }
  .services-card__info {
    position: relative;
  }
  .services-card__info-inner {
    padding: 5px;
    padding-right: 5px;
  }
}

.place-list {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  align-items: center;
  grid-gap: 20px;
}

.place-list__item:nth-child(1) {
  grid-column: span 2;
}

.place-list__item .card {
  opacity: 0;
}

@media (max-width: 1000px) {
  .place-list {
    grid-template-columns: repeat(3, 1fr);
  }
  .place-list__item:nth-child(1) {
    grid-column: span 3;
  }
}

@media (max-width: 700px) {
  .place-list {
    grid-template-columns: repeat(2, 1fr);
  }
  .place-list__item:nth-child(1) {
    grid-column: span 2;
  }
}

@media (max-width: 500px) {
  .place-list {
    grid-template-columns: repeat(1, 1fr);
  }
  .place-list__item:nth-child(1) {
    grid-column: span 1;
  }
}

.card {
  position: relative;
  background-color: #f7f9f8;
  border: 1px solid transparent;
  transition: all 0.7s linear;
}

.banner-list .card {
  background-color: #ffffff;
}

.card__img {
  max-height: 208px;
  overflow: hidden;
}

.card__img img {
  width: 100%;
}

.card__descr {
  padding: 30px 10px;
}

.card__title {
  height: 60px;
  font-size: 24px;
