}

.fold-table > thead tr th:last-child {
  text-align: center;
  border: 1px solid #08CC99;
  border-left: none;
  border-radius: 0 5px 5px 0;
}

.fold-table > tbody > tr.view td,
.fold-table > tbody > tr.view th {
  font-weight: 700;
  cursor: pointer;
}

.fold-table > tbody > tr.view td:first-child,
.fold-table > tbody > tr.view th:first-child {
  position: relative;
  padding-left: 100px;
}

.fold-table > tbody > tr.view td:first-child:before,
.fold-table > tbody > tr.view th:first-child:before {
  content: '';
  display: block;
  height: 12px;
  margin-top: -9px;
  pointer-events: none;
  position: absolute;
  right: 30px;
  top: 50%;
  transform-origin: 66% 66%;
  transform: rotate(45deg);
  transition: all 0.15s ease-in-out;
  width: 12px;
  border-bottom: 1px solid #D0D0D0;
  border-right: 1px solid #D0D0D0;
}

.fold-table > tbody > tr.view {
  background-color: #ffffff;
}

.fold-table > tbody > tr.view:hover {
  background: #FBFBFB;
  border: 1px solid #CACACA;
  border-radius: 5px;
  box-shadow: 0px 4px 13px rgba(0, 0, 0, 0.12);
}

.fold-table > tbody > tr.view.open {
  background-color: #ffffff;
}

.fold-table > tbody > tr.view:hover td:first-child:before,
.fold-table > tbody > tr.view:hover th:first-child:before {
  border-bottom: 1px solid #373737;
  border-right: 1px solid #373737;
}

.fold-table > tbody > tr.view.open td:first-child:before,
.fold-table > tbody > tr.view.open th:first-child:before {
  border-bottom: 1px solid #08CC99;
  border-right: 1px solid #08CC99;
  transform: rotate(-135deg);
}

.fold-table > tbody > tr.fold {
  display: none;
}

.fold-table > tbody > tr.fold.open {
  display: table-row;
}

