@media (max-width: 1300px) {
  .device-list__item {
    padding: 20px 40px;
  }
}

@media (max-width: 1200px) {
  .device-list__item span {
    width: 100%;
  }
}

@media (max-width: 1000px) {
  .device-list {
    grid-template-columns: repeat(1, 1fr);
    grid-template-rows: repeat(6, 1fr);
  }
  .device-list__item {
    flex-wrap: wrap;
    border-radius: 10px;
  }
  .device-list__item span {
    width: 100%;
    margin-top: 10px;
    margin-left: 0;
  }
  .device-list__item:nth-child(n+4) {
    justify-content: flex-start;
  }
}

.mobile-menu {
  position: absolute;
  top: 15px;
  left: 10px;
  display: none;
  flex-grow: 1;
  width: 35px;
  height: 18px;
  background-color: transparent;
  cursor: pointer;
}

.mobile-menu span {
  display: block;
  width: 100%;
  height: 3px;
  margin: 6px 0;
  border-radius: 10px;
  background: #08CC99;
}

.mobile-menu::before, .mobile-menu::after {
  content: '';
  display: block;
  width: 100%;
  height: 3px;
  background: #08CC99;
}

@media (max-width: 1000px) {
  .mobile-menu {
    display: block;
  }
}

.links-list {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  align-items: center;
  grid-gap: 20px;
}

@media (max-width: 1200px) {
  .links-list {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 600px) {
  .links-list {
    grid-template-columns: repeat(1, 1fr);
  }
}

.link-block {
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
  padding: 50px 0;
}

.link-block a {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
}

.link-block svg {
  width: 40px;
  height: 40px;
  margin-right: 20px;
  fill: #939393;
}

.link-block span {
  flex-basis: 85%;
  font-size: 15px;
  font-weight: 300;
  line-height: 26px;
}

.link-block:hover svg {
  fill: #286E56;
}

@media (max-width: 1200px) {
  .link-block {
    padding: 15px 0;
  }
}

.link-block-vertical {
  text-align: center;
}

.link-block-vertical img {
  display: block;
  margin: 0 auto;
}

.link-block-vertical a {
  color: #939393;
  font-size: 14px;
  line-height: 22px;
  text-decoration: underline;
}

.link-block-vertical a:hover {
  text-decoration: none;
}

@media (max-width: 790px) {
  .table-responsive thead th {
    display: none;
  }
  .fold-table > thead tr th:first-child {
    display: block;
    border-right: 1px solid #08CC99;
    border-radius: 5px;
    text-align: center;
  }
  .table-responsive tr {
    display: block;
  }
  .fold-content > table td::before {
    content: attr(data-title);
    display: block;
    padding: 5px 0;
    font-size: 15px;
    font-weight: 700;
    text-align: center;
  }
  .table-responsive tr td {
    display: block;
    width: 100%;
    text-align: center;
  }
  .table-responsive tr td.td__name::before {
    margin-bottom: 20px;
    color: #fff;
    background: #452f91;
  }
  .table__count {
    justify-content: center;
  }
  .table__delete {
    margin: 0 auto;
  }
  .table__size {
    padding-left: 0;
  }
  .table__price {
    font-size: 30px;
    color: #f73d34;
  }
  .table__price span {
    font-size: inherit;
  }
}

.social-list {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 35px 0;
}

.social-list__item {
  margin-right: 10px;
  list-style-type: none;
}

.social-list__icon {
  position: relative;
  display: block;
  width: 18px;
  height: 18px;
  background-image: url("../../img/twitter.png");
  background-size: auto;
  background-position: center;
  background-repeat: no-repeat;
}

.social-list__icon--fb {
  background-image: url("../../img/facebook.png");
}

.social-list__icon--vk {
  background-image: url("../../img/vk.png");
}

.social-list__icon--inst {
  background-image: url("../../img/inst.png");
}

.social-list__icon--ok {
  background-image: url("../../img/ok.png");
}

.social-list__icon--youtube {
  background-image: url("../../img/youtube.png");
}

.social-list__icon:hover {
  opacity: 0.5;
}

.perf-card__row .social-list {
  justify-content: flex-start;
  padding: 15px 0;
}

.perf-card__row .social-list .social-list__icon {
  width: 35px;
  height: 35px;
}

.page-footer__info .social-list {
  margin-right: 30px;
}

.copyright {
  color: #ffffff;
  font-size: 16px;
}

.page-footer {
  padding: 10px 0;
  background-color: #AEAEAE;
}

.page-footer__wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  grid-gap: 20px;
}

.page-footer__info {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: flex-end;
}

.page-footer__info-item {
  width: 50%;
}

.page-footer__phone {
  color: #ffffff;
  font-size: 16px;
  line-height: 26px;
}

.page-footer__phone:hover {
  color: #373737;
}

.page-footer__text {
  color: #ffffff;
  font-size: 15px;
  line-height: 24px;
}

@media (max-width: 1000px) {
  .page-footer__wrapper {
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 10px;
  }
  .page-footer__info {
    flex-wrap: wrap;
    justify-content: flex-start;
  }
  .page-footer__info-item {
    width: 100%;
    margin-bottom: 20px;
  }
  .page-footer__info-item:last-child {
    margin-bottom: 0;
  }
  .page-footer .social-list {
    padding: 10px 0;
  }
}
